import React, { useState } from "react";
import { getFolders } from "./api";
import "./sidebar.scss";

document.addEventListener("click", (evt) => {
  const menuEl = document.getElementById("menu");
  let targetElement = evt.target; // clicked element
  do {
      if (targetElement === menuEl) {
          // This is a click inside. Do nothing, just return.
          return;
      }
      // Go up the DOM
      targetElement = targetElement.parentNode;
  } while (targetElement);

  // This is a click outside.
  if (menuEl) {
    menuEl.style.display = 'none'
  }
});
const File = ({ file, setContent, noPadding }) => {
  const [menuOpen, setMenuOpen] = useState(false);
  return (
    <div className="file" style={{ paddingLeft: noPadding ? '0px' : ''}} onClick={() => setContent(file.title)} onContextMenu={(e) => {e.preventDefault();setMenuOpen(true)}}>
      <i className="fas fa-file"></i>
      <span className="title">{file.title}</span>
      {menuOpen && <div className="menu" id="menu" style={{display: menuOpen ? 'inherit' : 'none'}}>
        <ul onClick={(e) => e.stopPropagation()}>
          <li>Copy</li>
          <li>Cut</li>
          <li>Move</li>
          <li>Delete</li>
          <li>Properties</li>
        </ul>
      </div>}
    </div>
  );
};

const Folder = ({ folder = {}, depth = 0, setContent }) => {
  const subFolders = folder.folders;
  const files = folder.files;
  const [expand, setExpand] = useState(false);
  return (
    <div style={{ padding: `4px 0px 4px ${depth * 12}px` }}>
      <div className="folder" onClick={() => setExpand(!expand)}>
        {!expand ? (
          <i className="fas fa-folder" />
        ) : (
          <i className="fas fa-folder-open" />
        )}
        <span className="title">{folder.title}</span>
      </div>
      {expand && (
        <React.Fragment>
          {!files && !subFolders && <span className="empty-folder">Empty</span>}
          {files &&
            files.map(file => (
              <File key={file.id} file={file} setContent={setContent} />
            ))}
          {subFolders &&
            subFolders.map(folder => (
              <Folder
                key={folder.id}
                folder={folder}
                depth={depth + 1}
                setContent={setContent}
              />
            ))}
        </React.Fragment>
      )}
    </div>
  );
};

export default () => {
  const [content, setContent] = useState("");
  const folders = getFolders();
  return (
    <div className="folder-structure">
      <div className="sidebar">
        {folders.map(item => (
          item.type === 'FOLDER' ? <Folder
            key={item.id}
            folder={item}
            depth={0}
            setContent={setContent}
          /> : <File key={item.id} file={item} setContent={setContent} noPadding />
        ))}
      </div>
      <div className="content">{content}</div>
    </div>
  );
};
